import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button,Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import  Header  from "../Header";
import  Footer  from "../Footer";
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import Pagination from "@material-ui/lab/Pagination";
class AddCitySurge extends Component {
    
    state = {userId:'',cabTypes:[],surgeId:0,item:[],error:'',isLoading:false,loadingColor:'#ffffff',pageNo:0,cityName:'',cabType:'',surge:'',ertigaSurga:'',sedanSurge:'',
    innovaCrystaSurge:'',innovaSurge:'',inputeField:{},pageId:1,rowCount:0,totalPage:0};
    constructor(props) {
        super(props);    
        
      }
    
      componentDidMount() {    
          this.setState({isLoading:true});      
       let userId=localStorage.getItem("userId");
       this.setState({userId:userId});
       this.setState({pageId:1});
        //this.setState({item:this.props.match.params.data});
        this.getSurge(userId,1);
        this.getCabTypes(userId);
      }
    
      showPopup(object){
        
        this.prePayment(object);
      }
      
    async getCabTypes(userId) {
        try {
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId;
            let cabTypesData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cab_types?' + urlData, { headers });
            
            if (cabTypesData.data.code == 200) {
                console.log("CabTypes:"+JSON.stringify(cabTypesData.data.data));
                this.setState({ cabTypes: cabTypesData.data.data });
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });

    }
    async getSurge(userId,pageId){   
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId + "&pageId=" + pageId;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_surge?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                this.setState({ item: cabData.data.data });
                this.setState({totalPage:cabData.data.totalPage})
                this.setState({rowCount:cabData.data.rowCount})
            } else {

            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
       
          this.setState({isLoading:false});
          //this.setState({cabsList:data.data});
    }
    getSurgeByID=async(surgeId)=>{   
        try {
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&surgeId=" + surgeId;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_surgebyid?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                this.setState({ cityName: cabData.data.data.city });
                this.setState({ surgeId: cabData.data.data._id });
                let cabType="Compact";
                let surgeData=JSON.parse(cabData.data.data.surge);
                console.log(surgeData[cabType]+"===surgeData="+JSON.stringify(surgeData));
                console.log("CabTypes="+JSON.stringify(this.state.cabTypes));
                for (const [key, value] of Object.entries(this.state.cabTypes)) {
                    
                    let cabType=value['cabType'];
                    console.log(key+"===cabType======"+cabType);
                    //this.setState({cabType:surgeData[cabType]});
                    this.state.inputeField[cabType]=surgeData[cabType]
                }
                /*this.setState({sedanSurge:surgeData.Sedan});
                this.setState({innovaCrystaSurge:surgeData.InnovaCrysta});
                this.setState({innovaSurge:surgeData.Innova});
                this.setState({ertigaSurga:surgeData.SUVErtiga});
                this.setState({surge:surgeData.other});*/
                
                //cityName:'',cabType:'',surge:'',ertigaSurga:'',sedanSurge:'',innovaCrystaSurge:'',innovaSurge:''
            } else {

            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
       
          this.setState({isLoading:false});
          //this.setState({cabsList:data.data});
    }
    addSurge=async ()=>{
        let token=localStorage.getItem("token");
        let pageNo=this.state.pageNo+1;
        const headers = {'Authorization':`Bearer ${token}`} ;
        let carModelNo='';
        let carNo='';
        if(this.state.cityName=="" || this.state.cityName==null){
            this.setState({error:"Please enter city name"});
            return false;
        }
        try {
            this.setState({cabId:0})
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            /*const urlData1={
                userId:this.state.userId,
                cityName:this.state.cityName,
                sedanSurge:this.state.sedanSurge,
                surge:this.state.surge,
                innovaCrystaSurge:this.state.innovaCrystaSurge,
                innovaSurge:this.state.innovaSurge,
                ertigaSurga:this.state.ertigaSurga,
                isCity:'Y'
            }*/
            const urlData={
                userId:this.state.userId,
                cityName:this.state.cityName,
                surgedata:this.state.inputeField,
                isCity:'Y'
            }
            
            
            let cabData = await axios.post(process.env.REACT_APP_APIURL + 'admin/add_surge' , urlData, { headers });
            
            if (cabData.data.code == 200) {
                let pageId=this.state.pageId;
                this.getSurge(this.state.userId,pageId);
                this.setState({error:'Surge added successfully'});
            } else {
                this.setState({error:'Something went wrong'});
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });    
    }
    
    editSurge=async(dataObj)=>{
        let token=localStorage.getItem("token");
        let pageNo=this.state.pageNo+1;
        const headers = {'Authorization':`Bearer ${token}`} ;
        let carModelNo='';
        let carNo='';
        
        if(this.state.surgeId=="" || this.state.surgeId==null){
            this.setState({error:"Please Select city to update"});
            return false;
        }
        if(this.state.cityName=="" || this.state.cityName==null){
            this.setState({error:"Please enter city name"});
            return false;
        }
        
        try {
            this.setState({cabId:0})
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            /*const urlDataOld={
                userId:this.state.userId,
                cityName:this.state.cityName,
                sedanSurge:this.state.sedanSurge,
                surge:this.state.surge,
                innovaCrystaSurge:this.state.innovaCrystaSurge,
                innovaSurge:this.state.innovaSurge,
                ertigaSurga:this.state.ertigaSurga,
                ertigaSurga:this.state.ertigaSurga,
                surgeId:this.state.surgeId,
                isCity:'Y'
            }*/
            const urlData={
                userId:this.state.userId,
                cityName:this.state.cityName,
                surgeId:this.state.surgeId,
                surgedata:this.state.inputeField,
                isCity:'Y'
            }
            
            let cabData = await axios.put(process.env.REACT_APP_APIURL + 'admin/update_surge' , urlData, { headers });
            
            if (cabData.data.code == 200) {
                let pageId=this.state.pageId;
                this.getSurge(this.state.userId,pageId);
                this.setState({error:'Surge updated successfully'});
            } else {
                this.setState({error:'Something went wrong'});
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });    
    }
    deleteSurge=async(surgeId)=>{
        try {
            this.setState({cabId:0})
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            
            let urlData = "&userId=" + this.state.userId + "&surgeId=" + surgeId;
            let cabData = await axios.delete(process.env.REACT_APP_APIURL + 'admin/surge?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                let pageId=this.state.pageId;
                this.getSurge(this.state.userId,pageId);
                this.setState({error:'Surge deleted successfully'});
            } else {
                this.setState({error:'Something went wrong'});
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false }); 
    }
    setCityName =(cityName)=>{
        this.setState({cityName:cityName.target.value});
    }
    setSurge =(surge)=>{
        this.setState({surge:surge.target.value});
    }
    setSedanSurge=(surge)=>{
        this.setState({sedanSurge:surge.target.value});
    }
    setInnovaCrystaSurge=(surge)=>{
        this.setState({innovaCrystaSurge:surge.target.value});
    }
    setInnovaSurge=(surge)=>{
        this.setState({innovaSurge:surge.target.value});
    }
    setErtigaSurga=(surge)=>{
        this.setState({ertigaSurga:surge.target.value});
    }
    handleFormChange = (cabtype, event) => {
        
        console.log("cabtype:"+cabtype);
        console.log("value:"+event.target.value);
        this.setCabSurge(cabtype,event.target.value)
        
    }
    setCabSurge=(cabType,val)=>{
        //this.state.inputeField[cabType]=val;
        
        this.setState(prevState => {
            let inputeField = { ...prevState.inputeField };  // creating copy of state variable jasper
            inputeField[cabType] = val;                     // update the name property, assign a new value                 
            return { inputeField };                                 // return new object jasper object
          })
        //this.setState({...this.state.inputeField, cabType: val});
        console.log(cabType+":"+JSON.stringify(this.state.inputeField));
    }
    handlePageChange=async(event, value)=>{
        //this.setState({pageId:value});
        let userId=this.state.userId;
        let pageId=value;
        this.setState({pageId:pageId});
        this.getSurge(userId,pageId);
    }
    render() { 
        const override =`
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (
           
            <div> 
                <Header/> 
                <div>
                <ClipLoader css={override} style={{borderColor:'red'}} color={this.state.loadingColor} loading={this.state.isLoading}  size={150} >  </ClipLoader>
                </div>
                
                
                <div> 
                <section id="pricing" className="pricing">

                    <div className="container" data-aos="fade-up" style={{width:'95%!important'}}>
                        
                        <div className="row">                       
                            <div className="col-lg-12 col-md-12">
                                    
                                <Card>
                                        <Card.Title  className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>
                                            Add Surge
                                        </Card.Title>
                                        <Card.Body>
                                            <div style={{color:'red'}}>
                                                {this.state.error}
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>City Name <spam style={{color:'red'}}>*</spam></Form.Label>
                                                        <Form.Control name="cityName" type="text" placeholder="City Name" value={this.state.cityName} onChange={this.setCityName} />                                                                                                        
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                            {
                                                this.state.cabTypes.map((object, i) => {
                                                    return <div key={i}>
                                                            <Form.Group controlId="formBasicEmail" >
                                                                <Form.Label>{object.cabType} <spam style={{color:'red'}}>*</spam></Form.Label>
                                                                <Form.Control name={object.cabType} type="text" value={this.state.inputeField[object.cabType]} placeholder={object.cabType} onChange={event => this.handleFormChange(object.cabType, event)} />                                                                                                        
                                                            </Form.Group>
                                                        </div>                                                        
                                                    
                                                })
                                            }
                                            </div>
                                            
                                            <div className="col-12">        
                                                
                                                <Form.Group controlId="formBasicEmail" style={{float:'right'}}>
                                                    {this.state.surgeId!=''?<Button variant="primary" type="button" onClick={this.editSurge.bind(this)}>
                                                        Update Surge
                                                    </Button>:<Button variant="primary" type="button" onClick={this.addSurge.bind(this)}>
                                                        Add Surge
                                                    </Button>}
                                                                                                                                                           
                                                </Form.Group>
                                            </div>                                            
                                        </Card.Body>
                                    </Card>                                    
                            </div>             
                            
                        </div>
                        <br/>
                        <div className="row">                       
                            <div className="col-lg-12 col-md-12">
                                    
                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>City/District-wise surge </Card.Title>
                                        <Card.Body>
                                            <div style={{color:'red'}}>
                                            <Table striped bordered hover responsive>
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>City</th>
                                                        <th>Surge </th>
                                                        <th># </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {
                                                        this.state.item.map((object, i)=>{
                                                            return<tr key={i+1}>  
                                                                <td>{((this.state.pageId-1)*10)+(i+1)}</td>
                                                                <td>{object.city}</td>
                                                                <td>{object.surge}</td> 
                                                                <td>
                                                                    <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.deleteSurge.bind(this,object._id)}>
                                                                        <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                    </Button>&nbsp;&nbsp;                                                                     
                                                                     <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.getSurgeByID.bind(this,object._id)}>
                                                                        <i className="fa fa-eye iconStyle" aria-hidden="true"></i>
                                                                    </Button>                                                                    
                                                                </td>     
                                                            </tr>;
                                                        })
                                                    }
                                                    
                                                </tbody>    
                                            </Table>
                                            </div>
                                            <div>
                                            <Pagination
                                                className="paging"
                                                count={this.state.totalPage}
                                                page={this.state.pageId}
                                                defaultPage={this.state.pageId}
                                                siblingCount={2}
                                                boundaryCount={2}
                                                color="primary"
                                                showFirstButton showLastButton
                                                onChange={this.handlePageChange.bind()}
                                            />
                                            </div>
                                        </Card.Body>
                                    </Card>                                   
                            </div>      
                        </div>
                    </div>
                </section>
                 
                    
                </div>
            </div>
        );
    }
}
 
export default withRouter(AddCitySurge);